
import { defineComponent, computed, ref, watch } from 'vue'
import emmiter from '@/utils/eventBus'
import {
  coalTypes,
  TwoLevelCoalClass,
  transports,
  settlement,
  acceptance
} from '../config/data'
import { useStore } from 'vuex'
export default defineComponent({
  name: '',
  setup(props, { emit }) {
    const store = useStore()
    const contract = computed(() => store.state.contractModule.contract)
    const doubleDatePicker = ref(['', ''])
    emmiter.on('getContractDoubleTime', (times: any) => {
      doubleDatePicker.value = times
    })
    watch(
      doubleDatePicker,
      (newValue) => {
        store.commit('contractModule/setContractDoubleTime', newValue)
      },
      {
        deep: true
      }
    )
    const addLists = () => {
      store.state.contractModule.contract.tcitemList.push({
        calorievalue: '',
        citemid: '',
        coaltype: '',
        contractid: '',
        qty: ''
      })
    }
    const deleteList = (index: number) => {
      store.state.contractModule.contract.tcitemList.splice(index, 1)
    }
    const purchaseList = computed(() => store.state.purchaseApply.purchaseList)
    const upDateContract = (state: string): void => {
      store.dispatch('contractModule/updateContract', state)
      emit('closeDialog')
    }

    return {
      coalTypes,
      TwoLevelCoalClass,
      transports,
      settlement,
      purchaseList,
      doubleDatePicker,
      upDateContract,
      addLists,
      deleteList,
      contract,
      acceptance
    }
  }
})
